.skills {
  &__content-wrapper {
    display: flex;
    padding-left: 20px;
    gap: 20px;
    &__inner-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: centerk;
      &__catagory-text {
        display: inline-block;
        color: var(--yellow-theme-main-color);
        font-size: 12px;
        font-weight: 700;
        line-height: 1rem;
        letter-spacing: 0.5px;
        position: relative;
        text-transform: uppercase;
        padding: 5px 10px;
        border-bottom: 2px solid var(--yellow-theme-main-color);
      }
      &__catagory-text::before,
      &__catagory-text::after {
        position: absolute;
        content: "";
        width: 2px;
        height: 5px;
        background-color: var(--yellow-theme-main-color);
        bottom: -1px;
      }
      &__catagory-text::before {
        left: 0;
      }
      &__catagory-text::after {
        right: 0;
      }
      &__progressbar-container {
        width: 98%;
      }
      .progressbar-wrapper {
        padding: 50px 0px 0px 0px;
        p {
          font-size: 1.6rem;
          margin: 0 0 10px;
          font-weight: 500;
          text-transform: uppercase;
          color: var(--yellow-theme-sub-text-color);
        }
      }
    }
  }
 
}
@media only screen and (max-width:768px){
  .skills{
      &__content-wrapper{
          flex-direction: column;
      }

  }
  .skills__content-wrapper__inner-content__progressbar-container{
    margin-bottom: 20px;
  }
}